import React, { Component, MutableRefObject, ReactNode } from "react";
import { tsParticles, Container } from "tsparticles-engine";
import equal from "fast-deep-equal/react";
import type { IParticlesProps } from "./IParticlesProps";
import type { IParticlesState } from "./IParticlesState";
import type { ISourceOptions } from "tsparticles-engine";

/**
 * @param {{id?: string,width?: string,height?: string,options?: ISourceOptions,params?: ISourceOptions,style?: CSSProperties,className?: string,canvasClassName?: string,container?: RefObject<Container>}}
 */
export default class Particles extends Component<IParticlesProps, IParticlesState> {
    static defaultProps: IParticlesProps = {
        width: "100%",
        height: "100%",
        options: {},
        style: {},
        url: undefined,
        id: "tsparticles",
    };

    constructor(props: IParticlesProps) {
        super(props);

        this.state = {
            init: false,
            library: undefined,
        };
    }

    destroy(): void {
        console.log("destroy");

        if (!this.state.library) {
            return;
        }

        this.state.library.destroy();

        this.setState({
            library: undefined,
        });
    }

    shouldComponentUpdate(nextProps: Readonly<IParticlesProps>): boolean {
        console.log("should component update");

        return !equal(nextProps, this.props);
    }

    componentDidUpdate(): void {
        console.log("component did update");

        this.refresh();
    }

    forceUpdate(): void {
        console.log("force update");

        this.refresh();

        super.forceUpdate();
    }

    componentDidMount(): void {
        console.log("component did mount");

        (async () => {
            if (this.props.init) {
                await this.props.init(tsParticles);
            }

            this.setState(
                {
                    init: true,
                },
                () => {
                    this.loadParticles();
                }
            );
        })();
    }

    componentWillUnmount(): void {
        console.log("component will unmount");

        this.destroy();
    }

    render(): ReactNode {
        console.log("render");

        if (!this.state.init) {
            return <></>;
        }

        const { width, height, className, canvasClassName, id } = this.props;

        return (
            <div className={className} id={id}>
                <canvas
                    className={canvasClassName}
                    style={{
                        ...this.props.style,
                        width,
                        height,
                    }}
                />
            </div>
        );
    }

    private refresh(): void {
        console.log("refresh");

        this.destroy();

        this.loadParticles();
    }

    private loadParticles(): void {
        console.log("load particles");

        if (!this.state.init) {
            return;
        }

        const cb = async (container?: Container) => {
            if (this.props.container) {
                (this.props.container as MutableRefObject<Container>).current = container;
            }

            this.setState({
                library: container,
            });

            if (this.props.loaded) {
                await this.props.loaded(container);
            }
        };

        if (this.props.url) {
            tsParticles.loadJSON(this.props.id, this.props.url).then(cb);
        } else {
            tsParticles.load(this.props.id, this.props.params ?? this.props.options).then(cb);
        }
    }
}
